<template>
	<div class="edtorCon">
		<p class="text-center">用户信息编辑</p>
		<form>
			<p>用户账号：{{msg.identifier}}</p>
			<p><label for="nickName">昵&emsp;&emsp;称：</label><input type="text" name="nickName" id="nickName" v-model="msg.nickName"></p>
			<p><label for="heightUse">身高 /cm：</label><input type="Number" name="heightUse" id="heightUse" v-model="msg.stature"></p>
			<p><label for="weight">体重 /kg：</label><input type="Number" name="weight" id="weight" v-model="msg.weight"></p>
			<!-- <p><label for="birthday">生&emsp;&emsp;日：</label><input type="text" name="birthday" id="birthday"></p> -->
			<p><label for="status">账户状态：</label>
				<select v-model="msg.status" >
				    <option v-for="item in status.statusGrup" :value="item">
				    	<span v-if="item == 0">启用</span>
				    	<span v-if="item == 1">禁用</span>
				    </option>
				</select>
				<!-- <input type="text" name="status" id="status" v-model="msg.status"> --></p>
			<p><label for="psw">密&emsp;&emsp;码：</label><input type="password" name="psw" id="psw" v-model="msg.psw" disabled="pswDisablede" ><span @click='showRePswF()'>修改</span></p>
			<p v-show="msg.repswShow"><label for="repsw">确认密码：</label><input type="password" name="repsw" id="repsw" disabled="pswDisablede" v-model="msg.repsw"></p>
			<p><label for="qx">用户权限：</label><!-- <input type="text" name="qx" id="qx" v-model="msg.role"> -->
				<select v-model="msg.role" >
				    <option v-for="item in role.roleGrup" :value="item">
				    	<span v-if="item == 10">普通用户</span>
				    	<span v-if="item == 100">售后</span>
				    	<span v-if="item == 1000">工程师</span>
				    	<span v-if="item == 10000">管理员</span>
				    </option>
				</select>
			</p>
			<p class="text-center Prompt">{{msg.Prompt}}</p>
			<p class="text-center" ><button type="button" @click="sumbitF">保存修改</button></p>
		</form>

		<!-- 养老院用户数据处理 -->
		<!-- <p class="text-center" v-show='false'>病情处理</p>
		<div class="olderGorup text-center" v-show='false'>
			<div class="overflower">
				<div class="floatL">姓名：张大爷</div>
				<div class="floatL">年龄：68</div>
				<div class="floatL">性别：男</div>
			</div>
			<div>
				<div>
					<span>血压：</span>
					<span><input type="radio" name="blood"  checked="true" value="1">正常</span>
					<span><input type="radio" name="blood" checked="false" value='0'>异常</span>
				</div>
				<div>
					<span>血痒：</span>
					<span><input type="radio" name="bloodItch" value="1">正常</span>
					<span><input type="radio" name="bloodItch" value='0'>异常</span>
				</div>
				<div>
					<span>心率：</span>
					<span><input type="radio" name="heart" value="1">正常</span>
					<span><input type="radio" name="heart" value='0'>异常</span>
				</div>
			</div>
			<div><button>确认</button></div>
		</div> -->


	</div>

	
</template>
<script>
    export default {
    	'name': 'useEditor',
    	props:{
    		"msg":{
	    		type:[Object,Array]
	    	},
	    	'isShow':Boolean
    	},
    	
    	data: function(){
    		return {
    			pswDisablede:false,
    			status:{
    				statusGrup:[0,1]
    			},
    			role:{
    				roleGrup:[10,100,1000,10000]    // 10:普通用户  100：售后  1000 工程师  10000:管理园
    			},
    			formMsg:{},
    			useEditMsg:{},
    			testStature:/^[1-3]{1}[0-9]{2}$/,   //身高的正则
    			testWight:/^[1-9]{1}[0-9]{1,2}$/,   //体重的正则
    			tesPsw:/^[0-9]{1,6}$/,              //密码的验证码
    			resMsg:""
    		}
    	},
    	updated:function(){    //数据渲染完之后才进行的操作
    		if(this.msg.repswShow == false){
    			$("#psw").attr("disabled","disabled");
    			$("#repsw").attr("disabled","disabled");
    		}
    	},
    	mounted:function(){
    		this.useEditMsg.token = window.localStorage.getItem("token");
    	},
    	methods: {
    		showRePswF: function(){
    			this.msg.repswShow = true
    			$("#psw").removeAttr("disabled");
    			$("#repsw").removeAttr("disabled");
    			
    		},
    		selectChange: function(){
    			// this.msg.status = this.status.selcted;
    		},
    		sumbitF: function(){
    			if(this.msg.repswShow == true){
    				if(this.msg.nickName == '' ){
	    				this.msg.Prompt = "昵称不能为空"
	    			}else if(!this.testStature.test(Number(this.msg.stature))){
	    				this.msg.Prompt = "请输入正确的身高，如：182"
	    			}else if(!this.testWight.test(Number(this.msg.weight))){
	    				this.msg.Prompt = "请输入正确的体重，如：80"
	    			}else if(this.msg.psw == '' || !this.tesPsw.test(Number(this.msg.psw))){
	    				this.msg.Prompt = "密码必须为1-6位的数字 "
	    			}else if(this.msg.psw != this.msg.repsw){
	    				this.msg.Prompt = "两次密码不一致，请重新输入"
	    			}else {
	    				this.msg.Prompt = '';
	    				this.formMsg = {
	    					'identifier':this.msg.identifier,
		    				'nickName':this.msg.nickName,
		    				'stature':this.msg.stature,
		    				'weight':this.msg.weight,
		    				'role':this.msg.role,    //用户权限
		    				'psw':this.getpsw,
		    				'status':this.msg.status
		    			}
		    			this.useEditMsg.formMsg = JSON.stringify(this.formMsg);
		    			this.postUrlF(); //接口调用
	    			}
    			}else if(this.msg.repswShow == false){
    				if(this.msg.nickName == '' ){
	    				this.msg.Prompt = "昵称不能为空"
	    			}else if(!this.testStature.test(Number(this.msg.stature))){
	    				this.msg.Prompt = "请输入正确的身高，如：182"
	    			}else if(!this.testWight.test(Number(this.msg.weight))){
	    				this.msg.Prompt = "请输入正确的体重，如：80"
	    			}else {
	    				this.msg.Prompt = '';

	    				this.formMsg = {
	    					'identifier':this.msg.identifier,
		    				'nickName':this.msg.nickName,
		    				'stature':this.msg.stature,
		    				'weight':this.msg.weight,
		    				'role':this.msg.role,    //用户权限
		    				'psw':this.getpsw,
		    				'status':this.msg.status
		    			}
		    			this.useEditMsg.formMsg = JSON.stringify(this.formMsg);
		    			this.postUrlF(); //接口调用
	    			}
    			}
    		},
    		postUrlF: function(){   //更改用户信息的后台接口
    			// console.log(this.formMsg,"formMsg")
    			this.$emit("form-btn-click",this.useEditMsg);
    		}
    	},
    	computed: {
    		getpsw: function(){
    			return this.msg.psw
    		},
    		getrepsw: function(){
    			return this.msg.repsw
    		},
    		
    	}
    }
</script>
<style scoped>
.edtorCon>p{
	margin-bottom: 50px;
	font-weight: bold;
}
form>p{
	height: auto;
	margin-top: 10px;

}
form>p>input,form>p>select{
	height: 25px;
	padding-left: 10px;
	border:1px solid #495B69;
	outline: none;
}
form>p>select{
	    width: 273px;
    margin-left: -5px;
}
form>p>span{
	display: inline-block;
	padding:5px;
	background: #EE3D6E;
	font-size: 12px;
	color: white;
	margin-left: 5px;
	border-radius: 5px;
}
form>p>span:hover{
	cursor: pointer;
}
button{
 		width: 88px;
 		height: 25px;
 		line-height: 25px;
 		font-size: 13px;
 		color: white;
 		background: #EE3D6E;
 		border-radius: 5px;
 		border:none;
 		margin-top: 20px;
}
.Prompt{
	font-size: 14px;
	color: #EE3D6E;
}

/*老人院的用户的css*/
.olderGorup>div:nth-of-type(1){
	/*padding-bottom: 20px;*/
}
.olderGorup>div:nth-of-type(1)>div{
	margin-left: 30px;
}
.olderGorup>div:nth-of-type(1)>div:nth-of-type(1){
	margin-left: 0;
}
.olderGorup>div:nth-of-type(2){
	/*border: 1px solid red;*/
	margin: 0 auto;
	width: auto;
}
.olderGorup>div:nth-of-type(2)>div{
	margin-top: 25px;
}
.olderGorup>div:nth-of-type(2)>div>span:nth-of-type(3){
	margin-left: 20px;
	display: inline-block;
}
.olderGorup>div:nth-of-type(3){
	margin-top: 30px;
}
</style>



