
<!-- 柱形图 的组件 -->
<template>
  <!--<div class="graph">-->
    <div class="move_top overflower">
      <div class="floatL">{{msg.titile}}</div>
      <div class="floatR overflower">

        <button  class="floatR" @click='search'>搜索</button>
        <div class='datepicker floatR'>
          <datepicker v-on:picked="picked"></datepicker>
        </div>

      </div>
    </div>

</template>
<script>

import datepicker from './datepicker.vue'

import searchTime from '../../assets/JS/searchTime.js'  //兄弟组件的传值的事件机制
    export default {
    	props:{
    		'msg':{
    			type:Object
    		}
    	},
    	data:function(){
    		return {
          time:{
            year:null,
            month:null,
            day:null,
            type:null
          }
    		}
    	},
    	components: {
	        datepicker
	    },
	    mounted: function(){

	    },
//      mounted end
    	methods:{
        picked: function(year,month,day){
          this.time.year = year
          this.time.month = month
          this.time.day = day
          this.time.type = this.msg.type
        },
        search: function(){
          searchTime.$emit('timePicke',this.time)
          this.$emit('searchTime',this.time)
        }
     }
}
</script>
<style scoped src=''>
.move_top{
	padding: 20px 30px;
	background: #16B398;
}
.move_top>div:nth-of-type(1){
	color:white;
	font-size: 18px;
	line-height: 29px;
}
.move_top>div:nth-of-type(2)>button{
	width: 50px;
	height: 29px;
	background: #2B3946;
	color: white;
	font-size: 12px;
	border: none;
	border-radius: 5px;
}
.move_top>div:nth-of-type(2)>button:hover{
  cursor: pointer;
  background-color: #EE3D6E;
}
.datepicker{
  width: 100px;
  right: 600px;
  /*border: 1px solid red;*/
}
</style>



