<template>

  <!-- 数据图形 -->
  <div class="dataC" v-if='isShow'><!--  isShow -->
    <div>{{msg.min}}</div>
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
    <div id="bloodItch" class="dataCavas" style="width: 100%;height:300px;"></div>
    <div hidden = ''>{{msg.params.x}}</div>
  </div>

</template>
<script>

  import { setGraphOpt } from '../../../assets/JS/common.js'
  export default {
    props:{
      'msg':{
        type:Object
      },
        'isShow':{
          type:Boolean
        }
    },
    data: function(){
      return {

      }
    },
    updated: function(){
      this.drawing();
    },
    mounted: function(){
      this.drawing();
    },
    methods: {
      drawing: function(){
        if(this.isShow == true){
          var bloodItch = this.$echarts.init(document.getElementById('bloodItch'));
    // 指定图表的配置项和数据
          var x =this.getMsgX
          var y =  this.getMsgY;
          var option_bloodItch = setGraphOpt('血氧含量','y/mmHg',x,y)
    // 使用刚指定的配置项和数据显示图表。
          bloodItch.setOption(option_bloodItch);
          window.addEventListener("resize",function(){
            bloodItch.resize();
          });
        }
      }
    },
    computed: {
      getMsgX: function(){
          return this.msg.params.x
        },
        getMsgY: function(){
          return this.msg.params.y
        },
    }
  }
</script>
<style scoped>

  .dataC{
    background: white;
  }
</style>



