<template>

  <!-- 数据图形 -->
  <div class="dataC" v-if='isShow'>
    <div>{{msg.min}}</div>
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
    <div id="blood" class="dataCavas" style="width: 100%;height:300px;"></div>
    <div hidden>{{msg.params.gdx}}</div>
  </div>

</template>
<script>

  import { setGraphOpt } from '../../../assets/JS/common.js'
  export default {
    props:{
      'msg':{
        type:Object
      },
        'isShow':{
          type:Boolean
        }
    },
    data: function(){
      return {

      }
    },
    updated: function(){
      this.drawing();
    },
    mounted: function(){
      this.drawing();
    },
    // (ssy*1000+szy)
    methods: {
      drawing: function(){
        if(this.isShow == true){
            var blood = this.$echarts.init(document.getElementById('blood'));
            var option_blood = {
                title: [{
                    left: 'center',
                    text: '舒张压（高压）'
                }, {
                    top: '55%',
                    left: 'center',
                    text: '收缩压（低压）'
                }],
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: [{
                    // data: dateList
                    data: this.msg.params.gdx //szy 舒张压 X 坐标
                }, {
                    // data: dateList,
                    data: this.msg.params.gdx ,   //ssy 收缩压 X 坐标
                    gridIndex: 1
                }],
                yAxis: [{
                  name:'舒张压（低压）',
                    splitLine: {show: false}
                }, {
                  name:'收缩压（高压）',
                    splitLine: {show: false},
                    gridIndex: 1
                }],
                grid: [{
                    bottom: '60%'
                }, {
                    top: '60%'
                }],
                series: [{
                    type: 'line',
                    showSymbol: false,
                    // data: valueList
                    data: this.msg.params.szy   //szy 舒张压 Y 坐标
                }, {
                    type: 'line',
                    showSymbol: false,
                    data: this.msg.params.ssy,    // ssy收缩压 Y 坐标
                    xAxisIndex: 1,
                    yAxisIndex: 1
                }]
            };
            // 使用刚指定的配置项和数据显示图表。
            blood.setOption(this.setOption);
            window.addEventListener("resize",function(){
            blood.resize();
          });
        }

      }
    },
    computed: {
      setOption: function(){
        var colors = ['#5793f3', '#d14a61', '#675bba'];
        var option = {
          color: colors,
          tooltip: {
             trigger: 'axis',
            axisPointer: {
              type: 'cross'
            }
          },
          legend: {
            data:['舒张压 （低压）', '收缩压 （高压）']
          },
          grid: {
            top: 70,
            bottom: 50
          },
          dataZoom: [
             // {startValue: '00:00'}, {type: 'inside'}
            ],
          xAxis: [
            {
              type: 'category',
              axisTick: {
                alignWithLabel: true
              },
              axisLine: {
                onZero: false,
                lineStyle: {
                  color: colors[1]
                }
              },
              axisPointer: {
                label: {
                  formatter: function (params) {
                    return '收缩压' + params.value
                      + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
                  }
                }
              },
              data: this.msg.params.gdx //szy 舒张压 X 坐标
            },
            {
              type: 'category',
              axisTick: {
                alignWithLabel: true
              },
              axisLine: {
                onZero: false,
                lineStyle: {
                  color: colors[0]
                }
              },
              axisPointer: {
                label: {
                  formatter: function (params) {
                    return '舒张压 ' + params.value
                      + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
                  }
                }
              },
              data: this.msg.params.gdx //szy 舒张压 X 坐标
              
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name:'舒张压 （低压）',
              type:'line',
              xAxisIndex: 1,
              smooth: true,
              data: this.msg.params.szy   //szy 舒张压 Y 坐标
            },
            {
              name:'收缩压 （高压）',
              type:'line',
              smooth: true,
              data: this.msg.params.ssy,    // ssy收缩压 Y 坐标
            }
          ]
        };
        return option;
      }
    }
  }
</script>
<style scoped src=''>

  .dataC{
    background: white;
  }
</style>



