<template>
	<!-- <div class="searchC"> -->
          	<div class="overflower chooseC">
          		<div class="floatL title">{{msg.title}}：</div>
          		<ul>
          			<li 
                v-for="(item,index) in msg.data" 
                :class="{ action: checkAction(index)}"  
                @click="toggleSection(index)">{{item}}</li>
          		</ul>
          	</div>
</template>
<script>
    export default {
    	'name':'choose',
    	props:{
    		'msg': {
    			type:Object

    		}
    	},
      data: function(){
        return {
          nowIndexes:[0],   //下标
          nowChooseData:[]  //数据
        }
      },
      methods: {
        toggleSection: function(index){
          if(this.nowIndexes.indexOf(index) === -1){
            if(index !== 0){
              var num = this.nowIndexes.indexOf(0);
              if(num !== -1){
              this.nowIndexes.splice(num,1);
              }
              this.nowIndexes.push(index);
            }else if(index === 0){
              this.nowIndexes.splice(0,this.nowIndexes.length);
              this.nowIndexes.push(0);
            }
          }else{
            var num = this.nowIndexes.indexOf(index);
            this.nowIndexes.splice(num,1);
            if(this.nowIndexes.length === 0){
              this.nowIndexes.push(0);
            }
          }
          this.nowChooseData.splice(0,this.nowChooseData.length);  //添加数据之前先清空；
          for(var i=0;i<this.nowIndexes.length;i++){
            var num = this.nowIndexes[i];
            this.nowChooseData.push(this.msg.data[num]);  //添加数据
            
          }
          if(this.nowChooseData.length == this.msg.data.length - 1){
              this.nowChooseData.splice(0,this.nowChooseData.length);
              this.nowChooseData.push("不限");
            }
          this.$emit('on-change',this.nowIndexes,this.nowChooseData);
        },
        checkAction:function(index){
          return this.nowIndexes.indexOf(index) !== -1;
        }
      },
      

    }
</script>
<style scoped>
/*-------------------css在stageHome.css里面--------------*/
