	list-style: none;
	float: left;
	margin-left: 5px;
	padding: 5px 10px;
	border-radius: 5px;
	font-size: 15px;
}
.chooseC ul>li:hover{
  color: #16B398;
	cursor: pointer;
}
.chooseC .action{
  background: #16B398;
  color: white;
}
.chooseC .action:hover{
  color: white;
}*/
</style>



