<template>

  <!-- 数据图形 -->
  <div class="dataC" v-if='isShow'>
    <div>{{msg.min}}</div>
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
    <div id="heart" class="dataCavas" style="width: 100%;height:300px;"></div>
   <div hidden="">{{msg.params.x}}</div>
  </div>

</template>
<script>

  import { setGraphOpt } from '../../../assets/JS/common.js'
  import searchTime from '../../../assets/JS/searchTime.js'   //兄弟组件的传值的事件机制
    export default {
      props:{
        'msg':{
          type:Object
        },
        'isShow':{
          type:Boolean
        }
      },
      data: function(){
        return {
          
        }
      },
      updated:function(){

        this.drawing()
      },
      created: function(){

      },
      mounted: function(){
        this.drawing()
      },
      methods: {
      	drawing: function(){
          if(this.isShow == true){
            var heart = this.$echarts.init(document.getElementById('heart'));
          // 指定图表的配置项和数据
          // 使用刚指定的配置项和数据显示图表。
            heart.setOption(this.setOption);
            window.addEventListener("resize",function(){
                heart.resize();
            });
          }

      	}
      },
      computed: {
      	getMsgX: function(){
      		return this.msg.params.x
      	},
      	getMsgY: function(){
      		return this.msg.params.y
      	},
      	setOption: function(){
       		var option_heart = {
			      title: { text: '参考下图了解自己在安静状态下心率区间图',left: 'center' },
			     tooltip: {trigger: 'axis',formatter: '{a} <br/>{b} : {c}'},
			     legend: {
			         left: 'left',
			         data:  '心率的指数'
			     },
			     xAxis: {
			         type: 'category',
			         name: 'x',
			         splitLine: {show: false},
			         data: this.getMsgX  //x坐标
			     },
			     grid: {left: '3%', right: '4%',bottom: '3%',containLabel: true},
			     yAxis: {type: 'log', name: 'y'},
			     series: [
			         {
			             name: '心率值',
			             type: 'line',
			             data: this.getMsgY  //y坐标
			         }
			     ]
			 };

			return option_heart;
      	}
      },
    }
</script>
<style scoped src=''>

  .dataC{
    background: white;
  }
</style>



