<template>
  <div class="container">
    <section class="Stage_headerC">
      <after-header></after-header>
    </section>
    <section class="stage_content overflower">
      <div class="leftNav floatL text-center">
        <left-nav message="1"></left-nav>
      </div>
      <!-- <div class="rightWrap floatL"> -->
      	<router-view></router-view>
      <!-- </div> -->
    </section>
  </div>

</template>
<script >/*<!-- src=../../assets/JS/afterHome.js -->*/
import leftNav from './leftNav.vue'
export default {
	name:"afterHome",
	components: {
        'left-nav': leftNav
    },
	mounted: function(){
		var winH = $(window).height();
        $(".leftNav").height(winH);
        //窗口改变
        $(window).resize(function(){
          var winH = $(window).height();
          $(".leftNav").height(winH);
        })
	}
}
</script>
<style src="../../assets/css/backstage/stageHome.css"></style>



