<template>
  <ul>
    <li class="bgColorLi">
      <div>
         <router-link :to="{path:'/afterHome/usePage'}">用户</router-link>
      </div>
    </li>
    <li class="bgColorLi">
      <div>
         <router-link :to="{path:'/afterHome/exportData'}">导出数据</router-link>
      </div>
    </li>
    <!-- <li class="bgColorLi" >
      <div  v-bind:class="{ 'bgColor': message == 2}"  @click="open($event)">
          <router-link :to="{name:'MoveMent'}">生命特征</router-link>
      </div>
      <ul>
        <li><a href="#" @click="to_moveF()">运动趋势</a></li>
        <li><a href="#" @click="to_heartF()">心率</a></li>
        <li><a href="#" @click="to_bloodF()">血压</a></li>
        <li><a href="#" @click="to_Blood_itchF()">血氧</a></li>
        <li><a href="#" @click="to_ECGF()">心电图</a></li>
      </ul>
    </li> -->
  </ul>

</template>
<script>
import {moveSocrller} from '../../assets/JS/common.js'  //方法一
    export default {
      'name': 'leftNav',
      data: function(){
        return {
           i:0
        }
      },
      props: ['message'],
      mounted: function(){
        var bgColorLi = $(".bgColorLi")
        for(var i=0;i<bgColorLi.length;i++)
          bgColorLi.css("heigth","auto")
        },
      methods: {
        open: function(event){
          console.log(event.currentTarget.parentNode.style.height,"event.currentTarget")
          if(event.currentTarget.parentNode.style.height == '' || event.currentTarget.parentNode.style.height == 'auto'){
            event.currentTarget.parentNode.style.height = '40px';
          }else if(event.currentTarget.parentNode.style.height == '40px'){
            event.currentTarget.parentNode.style.height = 'auto';
          }
        },
        to_moveF: function(){
          moveSocrller($("#move").offset().top-60)
        },
        to_heartF: function(){
          moveSocrller($(".heart").offset().top-60)
        },
        to_bloodF: function(){
          moveSocrller($(".blood").offset().top-60)
        },
        to_Blood_itchF: function(){
          moveSocrller($(".Blood_itch").offset().top-60)
        },
        to_ECGF: function(){
          moveSocrller($(".ECG").offset().top-60)
        },
        
      }
    }
</script>



