function scaleF(obj, s, x, y) {
  //字体的缩放与移动
  obj.css({ transform: "scale(" + s + "," + s + ") translate(" + x + "px," + y + "px)" });
}
function scaleFTransition(obj, s, x, y, time) {
  //字体的缩放与移动
  obj.css({
    transform: "scale(" + s + "," + s + ") translate(" + x + "px," + y + "px)",
    transition: "all " + time + "s"
  });
}
function getTimesF() {
  var date = new Date();
  var time = date.toLocaleDateString().replace(/\//g, "-") + ' ' + timeToF(date.getHours()) + ':' + timeToF(date.getMinutes()) + ':' + timeToF(date.getSeconds());
  function timeToF(timeObj) {
    var getTime;
    if (timeObj < 10) {
      getTime = "0" + timeObj;
    } else {
      getTime = timeObj;
    }
    return getTime;
  }
  return time;
}
function getYearWeek(a, b, c) {
  //   计算年月日所在的  周   例：alert(getYearWeek(2017, 11, 30));
  var d1 = new Date(a, b - 1, c),
      d2 = new Date(a, 0, 1),
      d = Math.round((d1 - d2) / 86400000);
  return Math.ceil((d + (d2.getDay() + 1 - 1)) / 7);
};

function getWeekDay(year, index) {
  //计算周所在的  年月日   例：console.log(yugi(2017, 48))  
  var d = new Date(year, 0, 1);
  while (d.getDay() != 1) {
    d.setDate(d.getDate() + 1);
  }
  var to = new Date(year + 1, 0, 1);
  var i = 1;
  var arr = [];
  for (var from = d; from < to;) {
    if (i == index) {
      arr.push(from.getFullYear() + "-" + (from.getMonth() + 1) + "-" + from.getDate());
    }
    var j = 6;
    while (j > 0) {
      from.setDate(from.getDate() + 1);
      if (i == index) {
        arr.push(from.getFullYear() + "-" + (from.getMonth() + 1) + "-" + from.getDate());
      }
      j--;
    }
    if (i == index) {
      return arr;
    }
    from.setDate(from.getDate() + 1);
    i++;
  }
}
function formatDateTime(timeStamp) {
  //时间戳的转换
  var date = new Date();
  date.setTime(timeStamp);
  var y = date.getFullYear();
  var m = date.getMonth() + 1;
  m = m < 10 ? '0' + m : m;
  var d = date.getDate();
  d = d < 10 ? '0' + d : d;
  var h = date.getHours();
  h = h < 10 ? '0' + h : h;
  var minute = date.getMinutes();
  var second = date.getSeconds();
  minute = minute < 10 ? '0' + minute : minute;
  second = second < 10 ? '0' + second : second;
  return {
    year: y,
    month: m,
    day: d,
    hours: h,
    minute: minute,
    second: second
    // return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
  };
}
function getDaysInOneMonth(year, month) {
  //获取某月的天数
  month = parseInt(month, 10);
  var d = new Date(year, month, 0);
  return d.getDate();
}
function mutildF(nowChooseData, Smin) {
  var data = [];
  // age.push({'min':0,'max':666});
  for (var i = 0; i < nowChooseData.length; i++) {
    var min, max;
    if (nowChooseData[i] == "不限") {
      min = 0;
      max = 666;
    } else {
      if (nowChooseData[i].indexOf("-") !== -1) {
        min = Number(nowChooseData[i].split("-")[0]);
        max = Number(nowChooseData[i].split("-")[1]);
      } else {
        min = Smin;
        max = 666;
      }
    }

    data.push({ 'min': min, 'max': max });
  }
  return data;
}

//柱状图的options
function setGraphOpt(title, name, x, y) {
  var option_today = {
    title: {
      text: ''
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: { // 坐标轴指示器，坐标轴触发有效
        type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
      }
    },
    legend: {
      data: title
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: {
      name: 'x',
      data: x
    },
    yAxis: {
      name: name,
      type: 'value'
    },
    series: [{
      name: title,
      type: 'bar',
      // barWidth : '5',//柱图宽度
      barMaxWidth: '10', //最大宽度
      data: y,
      //配置样式
      itemStyle: {
        normal: {
          color: '#3EE8CE'
        }
      }
    }]
  };
  return option_today;
}

function resHttp(data, httpErr, loginErr, success) {
  if (data.flag == '222' || data.flag == '333' || data.flag == '000' || data.flag == 'sucess' && Number(data.data.res) == 0) {
    //服务器 异常   000 ：参数校验不通过
    httpErr();
  } else if (data.flag == '111') {
    //登陆校验失败，跳转到登陆页面
    loginErr();
  } else if (data.flag == 'success' && data.data.res == 1) {
    //成功
    success();
  }
}

