<template>
  <div id="app" v-if='ishow'>
    <!--<my-header></my-header>-->
    <router-view/>
    <!--<my-footer></my-footer>-->
  </div>
</template>

<script>
  import Vue from 'vue'



var vm = {
  name: 'app',
  data: function(){
    return {
      ishow:false
    }
  },
  mounted:function(){
    if(navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE",""))<10){
        this.ishow = false;
        alert("您的浏览器版本过低，请下载IE10及以上版本");
        
    }else{
      this.ishow = true;
    }
  }
}

export default vm;


</script>

<style>

</style>



