<template>

  <!-- 数据图形 -->
  <div class="dataC" v-if='isShow'>
    <div>{{msg.min}}</div>
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
    <div id="week" class="dataCavas" style="width: 100%;height:300px;"></div>
    <div hidden>{{msg.params.x}}</div>
  </div>

</template>
<script>
import { setGraphOpt } from '../../../assets/JS/common.js'
  export default {
    props:{
      'msg':{
        type:Object
      },
        'isShow':{
          type:Boolean
        }
    },
    data: function(){
      return {

      }
    },
    updated: function(){
      this.drawing()
      // console.log('this.msg.params.x=周',this.msg.params.x)
      // console.log('this.msg.params.y=周',this.msg.params.y)
    },
    mounted: function(){
       this.drawing()
    },
    methods: {
      drawing: function(){
        if(this.isShow == true){
            var week = this.$echarts.init(document.getElementById('week'));
    // 指定图表的配置项和数据
           var x =this.msg.params.x
           var y =  this.msg.params.y
           var option_week = setGraphOpt('周运动','y/m',x,y)
    // 使用刚指定的配置项和数据显示图表。
          week.setOption(option_week);
          window.addEventListener("resize",function(){
            week.resize();
          });
        }
        
      }
    }
  }
</script>
<style scoped src=''>

  .dataC{
    background: white;
  }
</style>



