<template>
	<div class="rightWrap floatL">
      <div>
      	<div class="curPosit">
          当前位置：<router-link :to="{path:'/afterHome/exportData'}"> 导出数据 </router-link> 
        </div>
        <div class="searchC">
	      	<my-choose :msg="adress"></my-choose>	
	      	<div class="overflower time">
	      		<div class="floatL">日期：</div>
	      		<ul>
	      			<li><input type="date" name=""></li>
	      			<li>-</li>
	      			<li><input type="date" name=""></li>
	      		</ul>
	      	</div>	
	      	<button type="button">查询</button>
	    </div>
	    <div class="exContent" id="exContent">
	    	<textarea style="width:100%;height:150px;" id="content">
	    		{{dataContent}}
	    	</textarea>
	    </div><!-- ./exportData.txt -->
	    <div class="download"><a download="data.txt" id="save-btn" @click="file"><button type="button">下载</button></a></div>
      </div>
  </div>
</template>
<script>
import choose from '../com/choose.vue'

    export default {
    	'name':'exportData',
    	data: function(){
    		return {
    			dataContent:'[{"birthday":23558400000,"hand":0,"id":"3bbce05beb58470aaba37f46d6384a3f","nickName":"18100000000","sex":0,"stature":0,"userAimStep":0,"userId":"80c59c949efa4dc2a323b739ccc5b36c","weight":0},{"birthday":23558400000,"hand":0,"id":"51414740f88b424b83ce20325b581fc2","nickName":"18200000000","sex":0,"stature":0,"userAimStep":0,"userId":"72ca077885ce40f9a4d6999a585367aa","weight":0},{"birthday":23558400000,"hand":0,"id":"5579313d71f14c829cfabca1f2ef8086","nickName":"18482132886","sex":0,"stature":0,"userAimStep":0,"userId":"6b9338bc77f44cbc83ce97911574f441","weight":0},{"birthday":23558400000,"hand":0,"id":"5eee770199934cc28c548400092ca9be","nickName":"18000000000","sex":0,"stature":0,"userAimStep":0,"userId":"84ddbb5c80e74be1b9de0282c73f2db1","weight":0},{"birthday":23558400000,"hand":0,"id":"5f1325051247416b9a774a03abf2eca3","nickName":"15000000000","sex":0,"stature":0,"userAimStep":0,"userId":"bd9f7868a5ae46f69d5cc3088ffa63cb","weight":0},{"birthday":23558400000,"hand":0,"id":"d007f9b9c65b47ff8c4f78c2c3615fc7","nickName":"18715736535","sex":0,"stature":0,"userAimStep":0,"userId":"c89827a8844540a0b01bf3f42e0961fe","weight":0}]' ,
    			adress:{'title':'城市','data':['不限','北京','上海','广州','不限','北京','上海','广州']},
    		}
    	},
    	components: {
      	myChoose:choose
      },
      methods: {
      	file:function(){

   			var content, saveBtn;

	        content = document.querySelector( '#content' );
	        saveBtn = document.querySelector( '#save-btn' );
	        content.addEventListener( 'change', function self(){
	            saveBtn.setAttribute( 'href', 'data:text/paint; utf-8,' + content.value );
	            return self;
	        }() );
		}
      }
    }
</script>
<style scoped>
.time{
	padding: 20px 0;
}
 	.time>div{
 		padding:5px 10px;
 	}
 	.time ul>li{
 		list-style: none;
 		float: left;
 		margin-left: 20px;
 	}
 	button{
 		width: 88px;
 		height: 25px;
 		line-height: 25px;
 		font-size: 13px;
 		color: white;
 		background: #EE3D6E;
 		border-radius: 5px;
 		border:none;
 	}
 	.searchC button{
 		
 		margin-left: 85px;

 	}
.exContent{
	background: white;
	padding:30px;
	margin-top: 20px;
}
.download{
	width: 100%;
	text-align: center;
	padding:30px 0;
}
</style>



