
import {cavasF_opt,getTimesF,formatDateTime,resHttp} from './common.js'
import leftNav from '../../components/afterWebsite/leftNav.vue'
import myDialog from '../../components/com/dialog.vue'
import myRemarks from '../../components/com/remarks.vue'
import datepicker from '../../components/com/datepicker.vue'
import graph from '../../components/com/graph.vue'
//画图开始
import today from '../../components/com/drawCarvas/today.vue'
import week from '../../components/com/drawCarvas/week.vue'
import month from '../../components/com/drawCarvas/month.vue'
import heart from '../../components/com/drawCarvas/heart.vue'
import blood from '../../components/com/drawCarvas/blood.vue'
import bloodItch from '../../components/com/drawCarvas/BloodItch.vue'
import myEcg from '../../components/com/drawCarvas/myEcg.vue'

var http = window.localStorage.getItem("http")

export default {
  'name':'MoveMent',
  data: function(){
    return {
      today:{titile:'天运动', min:'',type:'today',params:{x:[],y:[]},Show:false},
      week:{titile:'周运动', min:'',type:'week',params:{x:['周日','周一','周二','周三','周四','周五','周六'],y:[]},Show:false},
      month:{titile:'月运动', min:'',type:'month',params:{x:[],y:[]},Show:false},
      heart:{titile:'心率详情', min:'',type:'heart',params:{},Show:false},
      blood:{titile:'血压详情', min:'',type:'blood',params:{gdx:[],ssy:[],szy:[]},Show:false},
      bloodItch:{titile:'血氧详情', min:'',type:'bloodItch',params:{x:[],y:[]},Show:false},
      myEcg:{titile:'心电详情', min:'',type:'myEcg',params:{x:[],y:[]},Show:false},
      token: null,
      mydialogShow:false,
      params:{
        token:window.localStorage.getItem("token"),
        identifier:this.$route.query.identifier
      },
      userMsg:{
        identifier:"",
        nickName:"",
        deviceStatus:"",
        remark:""
      },
      editorRemark:{
        chooseBox:[],
        msgText:""
      },  //往编辑备注的弹框传的数据
      searchNum:"",
      propmtDialog:false,  //弹框的提示
      propmtDialogText:"",    //弹框的提示内容
    }
  },
  components: {
        'left-nav': leftNav,
        myDialog,
        myRemarks,
        graph,today,
        week,month,heart,blood,bloodItch,myEcg
      },
  mounted: function(){
    this.currDayTime = getTimesF();
    var winH = $(window).height();
    $(".stage_content>div:nth-of-type(1)").height(winH);
    //窗口改变
    $(window).resize(function(){
      var winH = $(window).height();
      $(".leftNav").height(winH);
    })
    this.getUseDtails();  //获取详情用户的信息 和备注的
    var  myDate = new Date();
    var todyTime = {
      year:myDate.getFullYear(),
      month:myDate.getMonth()+1,
      day:myDate.getDate()
    } 
    // var todyTime = {year:2018,month:1,day:1}
    this.getheart(todyTime);//获取心率的数据
    this.gettoday(todyTime);  ///获取天的函数
    this.getweek(todyTime);//调用周的函数
    this.getmonth(todyTime);//调用月的函数
    this.getblood(todyTime);//调用血压的函数
    this.getbloodItch(todyTime);//调用血痒的函数
    this.getmyEcg(todyTime);//调用心电的函数

  },
  methods: {
    searchNumF:function(){
      if(this.searchNum != ''){
        this.params.identifier = this.searchNum
        this.getUseDtails();
      }else{
        this.propmtDialogF("请输入正确的账号")
      }
    },
    checkMsg: function(){    //传数据给修改备注的弹框
      this.mydialogShow = true;
      var remark = this.userMsg.remark.split(",")
      var chooseBox = [];
      for(var i=0;i<remark.length;i++){
        if(remark[i].indexOf("msgText") == -1){
          if(i == 0){
            remark[0] = remark[0].split(":")[1]
          }
          this.editorRemark.chooseBox.push(remark[i])
        }else{
          this.editorRemark.msgText = remark[i].split(":")[1]
        }
      }
    },
    getUseDtails: function(){   //获取用户信息及备注
      this.$http({url: http+'/backstage/infoDetails',params: this.params,method: 'get'}).then(function (res) {
          var that = this;
          resHttp(res.body,function(){  //httpErr
            that.propmtDialogF("请求出错，请稍后再试")
             },function(){  //loginErr
              that.$router.push({path:'/login'})
             },function(){  //success
                that.$router.push({ path: '/afterHome/MoveMent', query: { identifier: that.params.identifier }})
                var data = JSON.parse(res.body.data.rows)
                that.userMsg.identifier = data.identifier;
                that.userMsg.nickName = data.nickName;
                that.userMsg.deviceStatus = data.deviceStatus;
                that.userMsg.remark = data.remark;
            })
      },function(err){
        this.propmtDialogF("系统繁忙，请稍后再试")
      })
    },
    getheart:function(date){  //心率
      let that = this
      this.getDate('/backstage/heartRate',0,date,function(res){
        if(res.body.data.rows != ''){
                that.heart.Show = true;
                var data = JSON.parse(res.body.data.rows)
                var times = [];
                data[0].results = JSON.parse(data[0].results)
                data[0].times = JSON.parse(data[0].times)
                for(var i=0;i<data[0].times.length;i++){
                  var tt = formatDateTime(data[0].times[i])
                  times.push(tt.hours + ':' + tt.minute)
                }
                that.heart.params = {
                  x:times,
                  y:data[0].results
                }
                // console.log('心率',data)
              }else{
                 that.heart.Show = false;
              }
      })
    },
    gettoday: function(date){   //今日计步
      let that = this
      this.getDate('/backstage/stepNumber',0,date,function(res){
        if(res.body.data.rows != ''){
            that.today.Show = true;
            var data = JSON.parse(res.body.data.rows)
            for(var i=0;i<data.length;i++){ 
              var tt = formatDateTime(data[i].dates)
              data[i].dates=tt.year + '-' + tt.month + '-' +tt.day
              data[i].results = JSON.parse(data[i].results)
            }
            that.today.params = {
              y:data[0].results
            }
        }else{
          that.today.Show = false;
        }
      })
    },
    getweek:function(date){
      let that = this
      this.getDate('/backstage/stepNumber',11,date,function(res){  //获取周的数据
        if(res.body.data.rows != ''){
                 that.week.Show = true;
                var data = JSON.parse(res.body.data.rows)
                var timeX = [],timeY = [], weekDay = ['周日','周一','周二','周三','周四','周五','周六'];
                for(var i=0;i<data.length;i++){ 
                  var tt = formatDateTime(data[i].dates)
                   data[i].dates=tt.year + '-' + tt.month + '-' +tt.day
                   data[i].results = JSON.parse(data[i].results)
                   data[i].total = 0;
                   for(var k=0;k<data[i].results.length;k++){
                      data[i].total += data[i].results[k]
                   }
                   var item = new Date(tt.year,tt.month-1,tt.day).getDay()
                   timeX.push(weekDay[item])
                   timeY.push(data[i].total)
                }
                that.week.params = {
                  x:timeX,
                  y:timeY
                }
              }else{
                 that.week.Show = false;
              }
      })
    },
    getmonth:function(date){
      let that = this
      this.getDate('/backstage/stepNumber',12,date,function(res){  //获取月的数据
        if(res.body.data.rows != ''){
                 that.month.Show = true;
                var data = JSON.parse(res.body.data.rows)
                var timeX = [],timeY = [];
                for(var i=0;i<data.length;i++){ 
                  var tt = formatDateTime(data[i].dates)
                   data[i].dates=tt.day
                   data[i].results = JSON.parse(data[i].results)
                   // data[i].total = parseInt(data[i].results.join('+'))
                   data[i].total = 0;
                   for(var k=0;k<data[i].results.length;k++){
                      data[i].total += data[i].results[k]
                   }
                   timeX.push(data[i].dates)
                   timeY.push(data[i].total)
                }
                that.month.params = {
                  x:timeX,
                  y:timeY
                }
              }else{
                 that.month.Show = false;
              }
      })
    },
    getblood: function(date){
      let that = this
      this.getDate('/backstage/bloodPressure',0,date,function(res){  //获取血压的数据
        if(res.body.data.rows != ''){
                 that.blood.Show = true;
                var data = JSON.parse(res.body.data.rows)
                //(ssy*1000+szy)
                var gdx = [], ssy = [],     //收缩压
                    szy = [],   //舒张压
                    gd = []  //解析后的收缩压与舒张压
                for(var i=0;i<data.length;i++){ 
                  let tt = formatDateTime(data[i].dates)
                   data[i].dates=tt.year + '-' + tt.month + '-' +tt.day
                   data[i].results = JSON.parse(data[i].results)
                   data[i].times = JSON.parse(data[i].times)
                   for(var j=0;j<data[i].results.length;j++){
                    gd.push((data[i].results[j]/1000).toString().split('.'));
                   }
                   for(var k = 0; k<data[i].times.length;k++){
                     let tt = formatDateTime(data[i].times[k])
                     data[i].times[k]=tt.hours + ':' + tt.minute
                   }
                    data[i]['gdx']=gd;
                    for(var n=0;n<data[i].gdx.length;n++){
                      ssy.push(Number(data[i].gdx[n][0]))
                      szy.push(Number(data[i].gdx[n][1]))
                    }
                }
                that.blood.params = {
                  gdx:data[0].times,
                  ssy:ssy,
                  szy:szy
                }
              }else{
                 that.blood.Show = false;
              }
      })
    },
    getbloodItch: function(date){
      let that = this
      this.getDate('/backstage/bloodOxygenContent',0,date,function(res){  //获取血痒的数据
        if(res.body.data.rows != ''){
                 that.bloodItch.Show = true;
                var data = JSON.parse(res.body.data.rows)
                for(var i=0;i<data.length;i++){
                  let tt = formatDateTime(data[i].dates)
                   data[i].dates=tt.year + '-' + tt.month + '-' +tt.day
                   data[i].results = JSON.parse(data[i].results)
                   data[i].times = JSON.parse(data[i].times)
                   for(var k = 0; k<data[i].times.length;k++){
                     let tt = formatDateTime(data[i].times[k])
                     data[i].times[k]=tt.hours + ':' + tt.minute
                   }
                }
                that.bloodItch.params = {
                  x:data[0].times,
                  y:data[0].results
                }
              }else{
                 that.bloodItch.Show = false;
              }
      })
    },
    getmyEcg: function(date){
      let that = this
      this.getDate('/backstage/electrocardiogram',13,date,function(res){  //获取心电的数据
        if(res.body.data.rows != ''){
                 that.myEcg.Show = true;
                var data = res.body.data.rows
                for(var key in data){
                  let tt = formatDateTime(key)
                  key = tt.year + '-' + tt.month + '-' +tt.day
                  // console.log(key)
                }
                // for(var i=0;i<data.length;i++){
                  // let tt = formatDateTime(data[i].dates)
                  //  data[i].dates=tt.year + '-' + tt.month + '-' +tt.day
                  //  data[i].results = JSON.parse(data[i].results)
                  //  data[i].times = JSON.parse(data[i].times)
                  //  for(var k = 0; k<data[i].times.length;k++){
                  //    let tt = formatDateTime(data[i].times[k])
                  //    data[i].times[k]=tt.minute + ':' + tt.second
                  //  }
                // }
                that.myEcg.params = {
                  // x:data[0].times,
                  // y:data[0].results
                }
                // console.log('获取心电的数据',data)
              }else{
                 that.myEcg.Show = false;
              }
      })
    },
    getDate: function(url,type,date,cb){/*获取数据*/
      var param = {
        token:this.params.token,
        identifier:this.params.identifier,
        plusType:type,
        date:date.year+'-'+date.month+'-'+date.day
      }
      this.$http({url: http+url,params: param,method: 'get'}).then(function (res) {
        var that = this;
          resHttp(res.body,function(){  //httpErr
            that.propmtDialogF("请求出错，请稍后再试")
             },function(){  //loginErr
              that.$router.push({path:'/login'})
             },function(){  //success
                 cb(res)
            })
           // cb(res)
      },function(err){
        this.propmtDialogF("系统繁忙，请稍后再试")
      })
    },
    Preservation: function(msg){   //保存按钮调用接口
      var myparms = {
        token:this.params.token,
      }
      var remark = {
          identifier:this.params.identifier,
          chooseBox:"",
          msgText:msg.msgText
        }
        remark.chooseBox = msg.chooseBox.join(",");
      myparms.remark = JSON.stringify(remark);
      this.$http({url: http+'/backstage/setRemark',params: myparms,method: 'put'}).then(function (res) {
            var that = this;
            resHttp(res.body,function(){  //httpErr
              that.propmtDialogF("请求出错，请稍后再试")
               },function(){  //loginErr
                that.$router.push({path:'/login'})
               },function(){  //success
                  that.mydialogShow = false;
                  that.propmtDialogF("保存成功")
                  that.getUseDtails();
              })
          },function(err){
              this.propmtDialogF("系统繁忙，请稍后再试")
          })


    },
    propmtDialogF: function(text){    //弹框显示1000毫秒后消失
      this.propmtDialog = true;
      this.propmtDialogText = text;
      setTimeout(this.closePropmtDialog,1000);
    },
    closePropmtDialog: function(){  //提示弹框的显示
      this.propmtDialog = false;
    },
    closeMyDilog:function(){
      this.mydialogShow = false;
    },
  }
}




