      <!-- <a id="product" href="#" @click="product">产品</a> -->
      <router-link :to="{name:'beforwebHome'}">产品</router-link>
      <router-link :to="{name:'our'}">关于我们</router-link>
      <router-link :to="{name:'APP'}">APP应用程序</router-link>
      <!-- <a href="#">APP应用程序</a> -->
      <a href="#">在线求助</a>
      <router-link :to="{name:'login'}" href="#">后台管理</router-link>
    </nav>
    <section class="ali"><router-link :to="{name:'alilogin'}">阿里邮箱</router-link></section>
  </header>

</template>
<script>
  import {moveSocrller} from '../../assets/JS/common.js'  //方法一
    export default {
      name:'header',
      mounted: function(){
        
      },
      methods: {
        product: function () {
          if(this.$route.path == '/'){
            moveSocrller($(".surface ").offset().top-100);  //方法一
          }else{
            this.$router.go('/beforwebHome');
          }
        }
      }
    }
</script>
<style></style>



