import Vue from 'vue'
import Router from 'vue-router'
import alilogin from '@/components/ali_Login'

import beforwebHome from '@/components/beforWebsite/index.vue'
import details from '@/components/beforWebsite/details.vue'
import our from '@/components/beforWebsite/our.vue'
import APP from '@/components/beforWebsite/APP.vue'
import login from '../components/afterWebsite/login.vue'


// import Home from '../components/afterWebsite/home.vue'

import afterHome from '../components/afterWebsite/afterHome.vue'
import usePage from '../components/afterWebsite/usePage.vue'
import exportData from '../components/afterWebsite/exportData.vue'



import MoveMent from '../components/afterWebsite/MoveMent.vue'



Vue.use(Router)

export default new Router({
	// mode:'history',    //去掉地址栏的#
  routes: [
    {path: '/',name: 'beforwebHome',component: beforwebHome},
    {path: '/beforwebHome',name: 'beforwebHome',component: beforwebHome},
    {path: '/details',name: 'details',component: details},
    {path: '/our',name: 'our',component: our},
    {path: '/APP',name: 'APP',component: APP},
    {path: '/login',name: 'login',component: login},
    {path: '/afterHome',name: 'afterHome', component: afterHome,
        children:[
            {path: '',name: 'usePage', component: usePage},
            {path: 'usePage',name: 'usePage', component: usePage},
            {path: 'MoveMent',name: 'MoveMent', component: MoveMent},
            {path: 'exportData',name: 'exportData', component: exportData},
        ]
    },
    {path: '/alilogin',name: 'alilogin',component: alilogin},
  ]
})



