<template>
  <div class="SG_header overflower">
    <div class="floatL">
      <!-- <a href=""><h1>智琢科技</h1></a> -->
      <router-link :to="{path:'/afterHome/usePage'}"><h1>智琢科技</h1></router-link>
    </div>
    <div class="floatR overflower">
      <div class="floatR"><img class="img-responsive" src="../../assets/img/stageImg/headeImg.jpg"></div>
      <div class="floatR headerTab" @click="loginOut">[退出]</div>
      <div class="floatR">{{nickName}}</div>
      <div class="floatR headerTab" @click="versionNumF">APP上传版本</div>
    </div>
    <my-dialog :isShow="versionShow"  @on-close="closePromtdia()">
      <div class="text-center versionFile">
        <p>版本号上传</p>
        <div><input type="file" id="filesIfo" @change='changeFile($event)' accept="application/octet-stream"/></div>
        <div><label>上传版本号：</label><input type="number" name="version" v-model='versionNum'><p style='font-size: 14px;color: #EE3D6E;'>{{propmt}}</p></div>
        <div><button @click='sendVersionContent($event)'>上传</button></div>
      </div>
    </my-dialog>
    <my-dialog :isShow="Promt_dialog" @on-close="closePromtdia()">{{dialogPromt}}</my-dialog>
  </div>
</template>
<script>
import dialog from '../com/dialog.vue'
import { resHttp } from '../../assets/JS/common.js'
var http = window.localStorage.getItem("http")
    export default {
      'name':'afterHeader',
      data: function(){
        return {
          Promt_dialog:false,
          dialogPromt:"",
          avatar:window.localStorage.getItem("avatar"),
          nickName:window.localStorage.getItem("nickName"),
          versionShow:false,
          versionContent:'',
          versionNum:null,
          propmt:''
        }
      },
      components: {
        myDialog:dialog
      },
      mounted: function(){
       var filesIfo = document.getElementById('filesIfo');   //上传文件的id
      },
      methods: {
        closePromtdia: function(){  //关闭弹框
          this.Promt_dialog = false;
          this.versionShow = false;
        },
        loginOut: function(){
          this.$http({url: http+'/backstage/logout',params: {token:window.localStorage.getItem("token")},method: 'delete'}).then(function (res) {
              var that = this;
              resHttp(res.body,function(){  //httpErr'
                that.propF(that,'出错了，请稍后再试')
                 },function(){  //loginErr
                  that.$router.push({path:'/login'})
                 },function(){  //success
                   that.$router.push({path:'/login'})
              })
          },function(res){
            that.propF(that,'系统出错，请稍后再试')
          })
        },
        versionNumF: function(){
          this.versionShow = true;
        },
        changeFile: function(e){  ///获取文件
          var file = e.target.files[0]
          var reader = new FileReader()
          var that = this
          reader.readAsDataURL(file)
          reader.onload = function(e) {
            console.log(e)
            that.versionContent = this.result  //Base64编码字符串
          }
        },
        sendVersionContent: function(e){   //上传文件
           var fileName = filesIfo.value.split('\\')
          if(!filesIfo.value){
            this.propmt = '请添加上传文件'
          }else if(this.versionNum == '' || this.versionNum == null){
            this.propmt = '请填写上传的版本号！'
          }else{
            let param = {
              'token':window.localStorage.getItem("token"),
              'imgStr':this.versionContent,
              'fileName':fileName[fileName.length-1],
              'version':Number(this.versionNum)
            }
            console.log('param=',param)
            this.$http({url: http+"/backstage/firmwareUpload",params: param,method: 'put'}).then(function (res) {
              var that = this;
              resHttp(res.body,function(){  //httpErr
                that.propF(that,'出错了，请稍后再试')
                 },function(){  //loginErr
                  that.$router.push({path:'/login'})
                 },function(){  //success
                  that.propF(that,'上传成功')
              })
            },function(res){

            })
          }
          
        },
        propF:function(that,msg){
          that.Promt_dialog = true;
          that.dialogPromt = msg
          setTimeout(that.closePromtdia,1000)
        }
      }
    }
</script>



