<template>
	<div class="dialog-wrap">
		<div class="mask" @click='colseSelf()' v-show="isShow"></div>
        <!-- <transition name="drop" class="drop"> -->
            <div class="dialog-content" v-show="isShow">
                <div class="close" @click='colseSelf()'>×</div>
                <slot></slot>
            </div>
        <!-- </transition>  -->
	</div>
</template>
<script>
    export default {
    	'name': 'dialog',
    	props:{
    		'isShow': {
    			type:Boolean
    		}
    	},
    	mounted: function(){
    		var winH = $(window).height();
    		$(".mask").height(winH);
    	},
    	methods: {
    		colseSelf:function(){
    			this.$emit("on-close");
    		}
    	}
    }
</script>
<style>
.drop-enter-active{
    transition: all 0.5s ease;
}
.drop-leave-active{
    transition: all 0.5s ease;
}
.drop-enter{
    transform: scale(0);
}
.drop-leave-active{
    transform: scale(0);
     
}
.dialog-wrap{
    width: 100%;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 3;
	padding: 0 !important;
}
 .mask{
 	width: 100%;
 	height: 100%;
 	background: rgba(0,0,0,0.4);
 }

 .dialog-content{
 	position: fixed;
 	height: auto;
 	box-sizing: border-box;
    background-color: white;
    left: 50%;
    top: 50%;
    /*transform: scale(0);*/
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    padding: 30px;
    box-sizing: border-box;
 }
 .close{
 	text-align: center;
 	line-height: 20px;
 	width: 20px;
 	height: 20px;
 	border-radius: 50%;
 	position: absolute;
 	right: 5px;
 	top: 5px;
 	font-size: 20px;
 }
  .close:hover{
  	background: rgba(0,0,0,0.4);
  	color: white;
  	cursor: pointer;
  }
</style>



