// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

require('es6-promise').polyfill();
import Vue from 'vue'
import App from './App'
import router from './router'
import vueResource from 'vue-resource'

import axios from 'axios'
Vue.prototype.$axios = axios
// 引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
import $ from 'jquery'   //jquery

require('./assets/css/common.css');   //官网的css
require('./assets/css/backstage/stageHeader.css');  //后台管理页面的 头部 和 左边导航 的css
// promise的polyfill

import header from './components/beforWebsite/header'
import footer from './components/beforWebsite/footer'
import afterHeader from './components/afterWebsite/header'
import myDialog from './components/com/dialog'

Vue.use(vueResource)

Vue.config.productionTip = false;

Vue.component('myHeader',header);
Vue.component('myFooter',footer);
Vue.component('afterHeader',afterHeader);
Vue.component('myDialog',myDialog);



new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  
})



