<template>

  <!-- 数据图形 -->
  <div class="dataC" v-if='isShow'>
    <div>{{msg.min}}</div>
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
    <div id="ECG" class="dataCavas" style="width: 100%;height:300px;"></div>
    <div hidden>{{msg.params.x}}</div>
  </div>

</template>
<script>
  import { setGraphOpt } from '../../../assets/JS/common.js'
  export default {
    props:{
      'msg':{
        type:Object
      },
        'isShow':{
          type:Boolean
        }
    },
    data: function(){
      return {
        x:['2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2014-06-01','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2','2016-1-2'],
        y:[10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200,10,89,112,30,50,200,100,112,30,50,200]
      }
    },
    updated: function(){
      this.drawing()
    },
    mounted: function(){
      this.drawing()
    },
    methods: {
      drawing: function(){
        if(this.isShow == true){
          var ECG = this.$echarts.init(document.getElementById('ECG'));
    // 使用刚指定的配置项和数据显示图表。
          ECG.setOption(this.setOption);
          window.addEventListener("resize",function(){
            ECG.resize();
        });
        }
      }
    },
    computed:{
      setOption: function(){
        var option_ECG = {
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
              data:this.x,
              axisLine: {
                  lineStyle: {
                      color: 'white'
                  }
              }
            },
            yAxis: {
                splitLine: {
                    show: false
                },
                axisLine: {
                  lineStyle: {
                      color: 'white'
                  }
              }
            },
            dataZoom: [
             {startValue: '2014-06-01'}, {type: 'inside'},

            ],

            visualMap: {
                top: 0,
                right: 0,
                // pieces: [
                //   {gt: 0,lte: 50,color: '#096'}, 
                //   {gt: 50,lte: 100,color: '#ffde33'}, 
                //   {gt: 100,lte: 150,color: 'green'}, 
                //   {gt: 150,lte: 200,color: '#cc0033'}, 
                //   {gt: 200,lte: 300,color: '#660099'}, 
                //   { gt: 300,color: '#7e0023'}
                // ],
                outOfRange: {
                    color: 'white'
                }
            },
            series: {
                name: '心电数据',
                type: 'line',
                data:this.y,
                markLine: {
                    silent: true,
                    data: [{ yAxis: 50}, {yAxis: 100 }, {yAxis: 150 }, {yAxis: 200}, {yAxis: 300}]
                }
            }
        };
        return option_ECG;
      }
    }
  }
</script>
<style scoped src=''>

  .dataC{
    background: black;
  }
</style>



