<template>
	<div class="remarksCon edtorCon" is-show="mydialogShow">
		<p class="text-center">备注信息</p>
		<form>
			<div class="overflower">
				<div class="floatL">异常类型：</div>
				<div  class="floatL">
					<p><input type="checkbox" v-model="msg.chooseBox" name="sport" value="sport"> 运动趋势</p>
					<p><input type="checkbox" v-model="msg.chooseBox" name="heartRate" value="heartRate"> 心率</p>
					<p><input type="checkbox" v-model="msg.chooseBox" name="blood" value="blood"> 血压</p>
					<p><input type="checkbox" v-model="msg.chooseBox" name="BloodItch" value="BloodItch"> 血氧</p>
				</div>
			</div>
			<div>
				<div class="floatL">备注信息：</div>
				<div  class="floatL">
					<textarea v-model="msg.msgText"></textarea>
				</div>
			</div>
			<!-- <p class="Prompt text-center">提示</p> -->
			<p class="text-center"><button type="button" @click="Preservation">保存</button></p>
		</form>
	</div>
</template>
<script>
    export default {
    	name:"remarks",
    	props:{
    		'mydialogShow':Boolean,
    		'msg':{
    			type:[Object]
    		}
    	},
    	data: function(){
    		return {
    		}
    	},
    	updated: function(){
    	},
    	mounted: function(){

    	},
    	methods:{
    		Preservation:function(){
    			if(this.msg.chooseBox[0] == undefined){
    				this.msg.chooseBox.splice(0,1);
    			}
    			for(var i=0;i<this.msg.chooseBox.length;i++){
    				if(this.msg.chooseBox[i] == ""){
    					console.log(this.msg.chooseBox[i],i)
    					this.msg.chooseBox.splice(i,1);
                        i--;//数组下标回退
    				}
    			}
                for(var i=0;i<this.msg.chooseBox.length;i++){
                    if (this.msg.chooseBox.indexOf(this.msg.chooseBox[i]) != i) {
                         this.msg.chooseBox.splice(i,1);  //删除数组元素后数组长度减1后面的元素前移
                         i--;//数组下标回退
                     }
                }
    			// console.log(this.msg.chooseBox,"this.msg.chooseBox");
    			this.$emit("on-change",this.msg)
    		},
    	},
    	computed: {
            
    	}

    }
</script>
<style scoped>
.remarksCon>p:first-child{
	margin-bottom: 50px;
	font-weight: bold;
}
form>div:nth-of-type(2){
	margin-top: 30px;
}
form>div>div>p{
	margin-top: 10px;
}
form>div>div>p:first-child{
	margin: 0;
}
textarea{
	height: 60px;
	border:2px solid #A9A9A9;
}
button{
 		width: 88px;
 		height: 25px;
 		line-height: 25px;
 		font-size: 13px;
 		color: white;
 		background: #EE3D6E;
 		border-radius: 5px;
 		border:none;
 		margin-top: 20px;
}
.Prompt{
	font-size: 14px;
	color: #EE3D6E;
}
</style>



