<template>
  <div class="container">
    <div class="login_content">
      <h1>智琢科技</h1>
      <form id="loginF" @submit.prevent="loginF">
        <div><label for="userName">用户名：</label><input type="text" v-model="userName"></div>
        <div><label for="psw">密&emsp;码：</label><input type="password" v-model="psw"></div>
        <div class="YZM overflower">
          <label for="checkNum">验证码：</label><input type="text" v-model="code"><!-- name="checkNum" -->
          <div  class="floatR"><img style="height:30px;" @click='getCode()' class="img-responsive" id="Code" :src='codeImg'></div><!-- ../../assets/img/YZM.png -->
        </div>
        <p>{{ Prompt }}</p><!-- 用户名或密码不正确，请重新输入！ -->
        <div class="loginBtn overflower">
          <button type="submit" class="floatL">登录</button>
          <button type="reset" class="floatR">重置</button>
        </div>
      </form>
    </div>
    <my-dialog :isShow="PromptDialogShow" @on-close="closePromtdia()">{{PromptDialog}}</my-dialog>
  </div>
</template>
<script> 
import dialog from '../com/dialog.vue'
import Qs from 'qs'
//设置请求的IP地址
localStorage.setItem("http",'http://120.78.30.114')  
// localStorage.setItem("http",'http://www.zhizhuotec.com')
// localStorage.setItem("http",'http://192.168.2.157:8080')
var http = window.localStorage.getItem("http")
    export default {
      'name':'login',
      data: function(){
        return {
          userName:null,
          psw:null,
          codeImg: http+'/backstage/imageCode',/*this.GLOBAL.comonUrl.imageCode*/
          code:null,
          Prompt:'',
          PromptDialogShow:false,   //提示弹框 是否显示
          PromptDialog:""    // 弹框提示内容
        }
      },
      components: {
        myDialog:dialog
      },
      mounted: function(){
        winHF();
        function winHF(){
          var winH = $(window).height();
          $(".login_content").height(winH);
        }
        $(window).resize(function(){
          winHF();
        })
      },
      methods: {
        closePromtdia: function(){
          this.PromptDialogShow = false;
        },
        loginF: function(){
          var getIp = window.localStorage.getItem("ip");   //获取ip
        ///     登陆接口
        //18715736535     // 18000000000     18615736535
        //p2791565     p2791565            p2791565

        var loginParme = {
          identityType:"phone",
          identifier:this.userName,
          credential:this.psw,
          loginIp:getIp
        }
       if(this.userName == null || this.userName == undefined){
        this.Prompt = '账号不能为空'
       }else if(this.psw == null || this.psw == undefined){
        this.Prompt = '密码不能为空'
       }else if(this.code == null || this.code == undefined){
        this.Prompt = '验证码不能为空'
       }else{
        this.Prompt = '';
        this.$http({url: http+'/backstage/verifyImageCode',params: {code:this.code},method: 'get'}).then(function (res) {
            if(res.body.flag == '000'){
              this.Prompt = '验证码错误'
            }else if(res.body.flag == 'success' && res.body.data.res == '0'){
              this.Prompt = res.body.data.msg
            }else if(res.body.flag == 'success' && res.body.data.res == '1'){
            this.$http({url: http+'/backstage/login',params: loginParme,method: 'get'}).then(function (res) {
                if(res.body.flag == '000'){
                  this.PromptDialogShow = true;
                  this.PromptDialog = "登录失败";
                  setTimeout(this.closePromtdia,1000);
                  this.getCode();
                }else if(res.body.flag == 'success' && res.body.data.res == '0'){
                  this.Prompt = res.body.data.msg
                  this.PromptDialogShow = true;
                  this.PromptDialog = res.body.data.msg;
                  setTimeout(this.closePromtdia,1000);
                  this.getCode();
                }else if(res.body.flag == 'success' && res.body.data.res == '1'){
                  localStorage.setItem("token",res.body.data.token); //设置token
                  localStorage.setItem("avatar",res.body.data.avatar); //头像地址
                  localStorage.setItem("nickName",res.body.data.nickName); //昵称
                  localStorage.setItem("role",res.body.data.role); //权限
                  this.$router.push({ path: '/afterHome' });
                }
              },function(err){
                this.getCode();
                this.PromptDialogShow = true;
                this.PromptDialog = "系统繁忙，请稍后再试";
                setTimeout(this.closePromtdia,1000);

              })
            }
          },function(err){
            this.PromptDialogShow = true;
            this.PromptDialog = "系统繁忙，请稍后再试";
            setTimeout(this.closePromtdia,1000);
          })
       }
        },
        getCode: function(){
          this.codeImg= http+'/backstage/imageCode?ab='+new Date().getTime();
          // this.codeImg= this.GLOBAL.comonUrl.imageCode;
        }
      }
    }
</script>
<style src="../../assets/css/backstage/login.css"></style>



